.sideBarOn,
.sideBarOff {
  position: absolute;
  top: 10px;
  width: 95%;
  height: 500px;
  background-color: #15113b;
  border-radius: 18px;
  z-index: 999;
  animation-duration: 750ms;
}
.sideBarOn .st1,
.sideBarOff .st1 {
  position: absolute;
  top: 8px;
  right: 10px;
  height: 35px;
  width: 35px;
}
.sideBarOn .st2,
.sideBarOn .st3,
.sideBarOff .st2,
.sideBarOff .st3 {
  position: absolute;
  top: 10px;
  right: -5px;
  height: 5px;
  width: 35px;
  margin: 5px;
  background-color: rgb(244, 244, 244);
}
.sideBarOn .st2,
.sideBarOff .st2 {
  rotate: 45deg;
}
.sideBarOn .st3,
.sideBarOff .st3 {
  rotate: -45deg;
}
.sideBarOn .sideBarLinks,
.sideBarOff .sideBarLinks {
  display: flex;
  flex-direction: column;
  font-size: 30px;
  font-weight: bold;
  margin-top: 83px;
  margin-left: 35px;
}
.sideBarOn .sideBarLinks a,
.sideBarOff .sideBarLinks a {
  margin: 12px 0px;
  color: rgb(244, 244, 244);
}

.sideBarOn {
  margin-left: 0%;
  animation-name: slideIn;
  transform: translateX(0%);
}

.sideBarOff {
  margin-left: 200%;
  animation-name: slideOut;
  transform: translateX(0%);
}

@keyframes slideIn {
  from {
    margin-left: 200%;
  }
  to {
    margin-left: 0%;
  }
}
@keyframes slideOut {
  from {
    margin-left: 0%;
  }
  to {
    margin-left: 200%;
  }
}
.sideBarInit {
  margin-left: 0%;
}
.sideBarInit .st1,
.sideBarInit .st2,
.sideBarInit .st3,
.sideBarInit .sideBarLinks {
  display: none;
}

